/**
 * WordPress dependencies
 */
import { cloneElement, render } from '@wordpress/element'
import domReady from '@wordpress/dom-ready'

export const colorizeIcon = SvgIcon => {
	return cloneElement( SvgIcon, {
		fill: 'url(#bookwormgb-gradient)',
		className: `bwgb-bookwormgb-icon-gradient ${ SvgIcon.props.className || '' }`,
	} )
}

// Add our SVG gradient placeholder definition that we'll reuse.
domReady( () => {
	const bookwormgbGradient = document.createElement( 'DIV' )
	document.querySelector( 'body' ).appendChild( bookwormgbGradient )
	render(
		<svg
			xmlns="http://www.w3.org/2000/svg"
			className="bwgb-bookwormgb-gradient"
			height="0"
			width="0"
			style={ { opacity: 0 } }
		>
			<defs>
				<linearGradient id="bookwormgb-gradient">
					<stop offset="0%" stopColor="#8c33da" stopOpacity="1" />
					<stop offset="100%" stopColor="#f75454" stopOpacity="1" />
				</linearGradient>
			</defs>
		</svg>,
		bookwormgbGradient
	)
} )

export const BookwormGBIconTextured = colorizeIcon( <SVGBookwormGBIcon width="20" height="20" /> )

