/**
 * External dependencies
 */
import {
	__,
	_n,
	sprintf
} from '@wordpress/i18n'
import PropTypes from 'prop-types'
import ErrorMessage from '~bookwormgb/components/woocommerce/components/error-placeholder/error-message.js'
import withSearchedPosts from './with-searched-posts.js'
import PostsSearchListControl from './search-list-control'

const PostsControl = ( {
	error,
	onChange,
	onSearch,
	selected,
	posts,
	isLoading,
} ) => {
	const messages = {
		clear: __( 'Clear all posts', 'bookwormgb' ),
		list: __( 'Posts', 'bookwormgb' ),
		noItems: __(
			"You doesn't have any posts.",
			'bookwormgb'
		),
		search: __(
			'Search for posts to display',
			'bookwormgb'
		),
		selected: n =>
			sprintf(
				_n(
					'%d post selected',
					'%d posts selected',
					n,
					'bookwormgb'
				),
				n
			),
		updated: __(
			'Post search results updated.',
			'bookwormgb'
		),
	}

	if ( error ) {
		return <ErrorMessage error={ error } />
	}

	return (
		<PostsSearchListControl
			className="bookwormgb-posts-control-posts"
			list={ posts }
			isLoading={ isLoading }
			selected={ posts.filter( ( { id } ) =>
				selected.includes( id )
			) }
			onSearch={ onSearch }
			onChange={ onChange }
			messages={ messages }
		/>
	)
}

PostsControl.propTypes = {
	onChange: PropTypes.func.isRequired,
	onSearch: PropTypes.func,
	selected: PropTypes.array,
	posts: PropTypes.array,
	isLoading: PropTypes.bool,
}

PostsControl.defaultProps = {
	selected: [],
	posts: [],
	isLoading: true,
}

export default withSearchedPosts( PostsControl )
