/**
 * External dependencies
 */
import {
	ControlSeparator,
	DesignControl,
	IconControl,
	URLInputControl,
	ColorPaletteControl
} from '~bookwormgb/components'

/**
 * Internal dependencies
 */
import ImageDesignBasic from './images/basic.png'
import ImageDesignGhost from './images/ghost.png'
import ImageDesignLink from './images/link.png'

/**
 * WordPress dependencies
 */
import {
	SelectControl,
	ToggleControl,
} from '@wordpress/components'
import {
	__
} from '@wordpress/i18n'
import { applyFilters } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { i18n } from 'bookwormgb'

const ButtonControls = props => {
	const design = props.design ? props.design : 'default'

	return (
		<Fragment>
			<Fragment>
				{ props.showUrl &&
					<Fragment>
						{ props.onChangeUrl && (
							<URLInputControl
								label={ __( 'Link / URL', i18n ) }
								value={ props.url }
								onChange={ props.onChangeUrl }
								placeholder="http://"
							/>
						) }
						{ props.onChangeUrl && props.onChangeNewTab && (
							<ToggleControl
								label={ __( 'Open link in new tab', i18n ) }
								checked={ props.newTab }
								onChange={ props.onChangeNewTab }
							/>
						) }
						{ props.onChangeUrl && props.onChangeNoFollow && (
							<ToggleControl
								label={ __( 'Nofollow link', i18n ) }
								checked={ props.noFollow }
								onChange={ props.onChangeNoFollow }
							/>
						) }

						{ props.onChangeUrl && <ControlSeparator /> }
					</Fragment>
				}
			</Fragment>

			{ props.onChangeDesign && (
				<DesignControl
					label={ __( 'Design', i18n ) }
					selected={ design }
					options={ [
						{ 
							label: __( 'Default', i18n ), value: 'default', image: ImageDesignBasic 
						},
						{ 
							label: __( 'Outline', i18n ), value: 'outline', image: ImageDesignGhost 
						},
						{ 
							label: __( 'Link', i18n ), value: 'link', image: ImageDesignLink 
						},
						...applyFilters( 'bookwormgb.button.edit.designs', [] ),
					] }
					onChange={ props.onChangeDesign }
				/>
			) }

			<ControlSeparator />

			{ props.onChangeBackground &&
				<ColorPaletteControl
					label={ __( 'Color', i18n ) }
					value={ props.background }
					onChange={ props.onChangeBackground }
					disableCustomColors={ true }
				/>
			}

			<ControlSeparator />

			{ props.onChangeSize &&
				<SelectControl
					label={ __( 'Size', i18n ) }
					value={ props.size }
					options={ [
						{ value: 'default', label: __( 'Default', i18n ) },
						{ value: 'btn-xs', label: __( 'Extra Small', i18n ) },
						{ value: 'btn-sm', label: __( 'Small', i18n ) },
						{ value: 'btn-lg', label: __( 'Large', i18n ) }
					] }
					onChange={ props.onChangeSize }
					className="bwgb--help-tip-button-size"
				/>
			}
			{ props.onChangeWide &&
				<ToggleControl
					label={ __( 'Is Wide ?', i18n ) }
					checked={ props.isWide }
					onChange={ props.onChangeWide }
				/>
			}
			{ props.onChangeWideSM &&
				<ToggleControl
					label={ __( 'Is Wide SM ?', i18n ) }
					checked={ props.isWideSM }
					onChange={ props.onChangeWideSM }
				/>
			}

			<ControlSeparator />

			{ ( design !== 'link' && props.showIcon ) &&
				<Fragment>
					{ props.onChangeIcon &&
						<IconControl
							label={ __( 'Icon', i18n ) }
							value={ props.icon }
							onChange={ props.onChangeIcon }
						/>
					}
					{ props.onChangeIconAfterText && 
						<ToggleControl
							label={ __( 'Is Icon After Text ?', i18n ) }
							checked={ props.isIconAfterText }
							onChange={ props.onChangeIconAfterText }
						/>
					}
					{ props.onChangeIconButton && 
						<ToggleControl
							label={ __( 'Is Icon Button ?', i18n ) }
							checked={ props.isIconButton }
							onChange={ props.onChangeIconButton }
						/>
					}
					<ControlSeparator />
				</Fragment>
			}

			{ ( props.onChangeBorderRadius && design !== 'link' ) &&
			<SelectControl
				label={ __( 'Border Radius', i18n ) }
				value={ props.borderRadius }
				options={ [
					{ value: 'default', label: __( 'Default', i18n ) },
					{ value: 'rounded-0', label: __( 'Rounded 0', i18n ) },
					{ value: 'rounded-md', label: __( 'Rounded Md', i18n ) },
					{ value: 'btn-pill', label: __( 'Pill', i18n ) },
					{ value: 'rounded-circle', label: __( 'Circle', i18n ) }
				] }
				onChange={ props.onChangeBorderRadius }
			/>
			}
			{ props.onChangeBlock && 
				<ToggleControl
					label={ __( 'Is Block ?', i18n ) }
					checked={ props.isBlock }
					onChange={ props.onChangeBlock }
				/>
			}
			{ props.onChangeDisable &&
				<ToggleControl
					label={ __( 'Is Disable Button ?', i18n ) }
					checked={ props.isDisable }
					onChange={ props.onChangeDisable }
				/>
			}
			{ props.onChangeTransition &&
				<ToggleControl
					label={ __( 'Enable Transition', i18n ) }
					checked={ props.isTransition }
					onChange={ props.onChangeTransition }
				/>
			}
		</Fragment>
	)
}

ButtonControls.defaultProps = {
	// Used for typography.
	hasTypography: true,
	showUrl: true,
	showIcon: true,
	attrNameTemplate: '%s',
	setAttributes: () => {},
	blockAttributes: {},

	design: 'default',
	onChangeDesign: () => {},

	url: '',
	newTab: '',
	noFollow: '',
	onChangeUrl: () => {},
	onChangeNewTab: () => {},
	onChangeNoFollow: () => {},

	background: '',
	onChangeBackground: () => {},

	size: 'default',
	onChangeSize: () => {},

	isWide: false,
	onChangeWide: () => {},

	isWideSM: false,
	onChangeWideSM: () => {},

	isDisable: false,
	onChangeDisable: () => {},

	isBlock: false,
	onChangeBlock: () => {},

	borderRadius: 'default',
	onChangeBorderRadius: () => {},

	isIconAfterText: true,
	onChangeIconAfterText: () => {},

	isIconButton: true,
	onChangeIconButton: () => {},

	isTransition: true,
	onChangeTransition: () => {},

	icon: '',
	onChangeIcon: () => {},
}

export default ButtonControls
