import { __ } from '@wordpress/i18n'
import { Fragment } from '@wordpress/element'
import { registerPlugin } from '@wordpress/plugins'
import {
	PluginSidebarMoreMenuItem,
	PluginSidebar
} from '@wordpress/edit-post'
import { i18n } from 'bookwormgb'
import { BookwormGBIconTextured } from '~bookwormgb/icons'

import PagePluginSidebar from './meta-fields/page'

const PluginCustomMetaFields = () => {
	return(
		<Fragment>
			<PluginSidebarMoreMenuItem
				target="bookworm-options-sidebar"
				icon={ BookwormGBIconTextured }
			>
				{ __( 'Bookworm Options', i18n ) }
			</PluginSidebarMoreMenuItem>
			<PluginSidebar name="bookworm-options-sidebar" title={ __( 'Bookworm Options', i18n ) }>
				<PagePluginSidebar />
			</PluginSidebar>
		</Fragment>
	)

}

registerPlugin( 'bwgb-sidebar', {
	icon: BookwormGBIconTextured,
	render: PluginCustomMetaFields
})
