/**
 * Creates all the attributes needed for the Button Controls component
 */
/**
 * External dependencies
 */
import { createAllCombinationAttributes } from '~bookwormgb/util'
import { i18n } from 'bookwormgb'

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'

/**
 * Internal dependencies
 */
import { omitAttributes, pickAttributes } from '../attributes'

const createButtonAttributes = ( attrNameTemplate, options = {} ) => {
	const {
		selector = '.bwgb-button',
		defaultText = __( 'Button text', i18n ),
		defaultDesign = 'default',
		defaultBackground= 'primary',
		defaultSize = 'default',
		defaultBorderRadius = 'default',
		defaultIcon = '',
		defaultIsBtn = false,
		defaultIsIconButton = false,
		defaultIsWide = false,
		defaultIsWideSM = false,
		defaultIsDisable = false,
		defaultIsTransition = false,
		defaultIsIconAfterText = true,
		defaultIsBlock = false,
		defaultNoFollow = false,
		exclude = [],
		include = [],
	} = options

	return pickAttributes( omitAttributes( {
		// Text
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'string',
				default: defaultText,
			},
			[
				'Text',
			]
		),

		// Url
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'string',
				source: 'attribute',
				selector,
				attribute: 'href',
				default: '',
			},
			[
				'Url',
			]
		),

		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'boolean',
				default: '',
			},
			[
				'NewTab',
			]
		),

		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'boolean',
				default: defaultNoFollow,
			},
			[
				'NoFollow',
			]
		),

		// Icon
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'string',
				default: defaultIcon,
			},
			[
				'Icon',
			]
		),

		// Design
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'string',
				default: defaultDesign,
			},
			[
				'Design',
			]
		),

		// Background
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'string',
				default: defaultBackground,
			},
			[
				'Background',
			]
		),

		// Size
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'string',
				default: defaultSize,
			},
			[
				'Size',
			]
		),

		// BorderRadius
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'string',
				default: defaultBorderRadius,
			},
			[
				'BorderRadius',
			]
		),

		// IsBtn
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'boolean',
				default: defaultIsBtn,
			},
			[
				'IsBtn',
			]
		),

		// IsIconButton
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'boolean',
				default: defaultIsIconButton,
			},
			[
				'IsIconButton',
			]
		),

		// IsWide
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'boolean',
				default: defaultIsWide,
			},
			[
				'IsWide',
			]
		),

		// IsWideSM
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'boolean',
				default: defaultIsWideSM,
			},
			[
				'IsWideSM',
			]
		),

		// IsBlock
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'boolean',
				default: defaultIsBlock,
			},
			[
				'IsBlock',
			]
		),

		// IsIconAfterText
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'boolean',
				default: defaultIsIconAfterText,
			},
			[
				'IsIconAfterText',
			]
		),

		// IsDisable
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'boolean',
				default: defaultIsDisable,
			},
			[
				'IsDisable',
			]
		),

		// IsTransition
		...createAllCombinationAttributes(
			attrNameTemplate,
			{
				type: 'boolean',
				default: defaultIsTransition,
			},
			[
				'IsTransition',
			]
		),
	}, exclude, attrNameTemplate ), include, attrNameTemplate )
}

export default createButtonAttributes

export const createButtonAttributeNames = attrNameTemplate => Object.keys( createButtonAttributes( attrNameTemplate ) )
