/**
 * External dependencies
 */
import {
	UrlInputPopover,
} from '~bookwormgb/components'
import { getIconClassName } from '~bookwormgb/components/icon-control'

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { Component, Fragment } from '@wordpress/element'
import classnames from 'classnames'
import { 
	i18n, paletteColor
} from 'bookwormgb'
import { RichText } from '@wordpress/block-editor'

// Deprecated ButtonEdit.Content methods.
export { default as ButtonEditHelper } from './helper'

let buttonInstanceId = 1

class ButtonEdit extends Component {
	constructor() {
		super( ...arguments )
		this.state = {
			openPopup: false,
		}
		this.buttonInstanceId = buttonInstanceId++

		this.onButtonClickHandler = this.onButtonClickHandler.bind( this )
		this.outsideClickHandler = this.outsideClickHandler.bind( this )
		this.onKeyPressHandler = this.onKeyPressHandler.bind( this )
	}

	onButtonClickHandler( event ) {
		if ( this.props.iconButton && event.target.closest( '.bwgb-svg-icon-placeholder__button' ) ) {
			// If this is an icon button, open the url popover if the icon is clicked.
		} else if ( event.target.closest( '.bwgb-svg-icon-placeholder__button' ) ||
			event.target.closest( '.bwgb-url-input-popover' ) ||
			event.target.closest( '.bwgb-icon-popover' ) ||
			event.target.closest( '.components-popover' )
		) {
			return
		}
		if ( ! this.state.openPopup ) {
			document.body.addEventListener( 'click', this.outsideClickHandler )
		}
		this.setState( { openPopup: true } )
	}

	outsideClickHandler( event ) {
		if ( ! event.target.closest( `.bwgb-button-container-${ this.buttonInstanceId }` ) &&
			! event.target.closest( '.bwgb-url-input-popover' ) &&
			! event.target.closest( '.components-popover' ) ) {
			this.hideUrlPopup()
		} else if ( this.props.iconButton && event.target.closest( '.bwgb-svg-icon-placeholder__button' ) ) {
			// If this is an icon button, don't close the url popover if the icon is clicked.
		} else if ( event.target.closest( '.bwgb-svg-icon-placeholder__button' ) ) {
			this.hideUrlPopup()
		}
	}

	hideUrlPopup = () => {
		document.body.removeEventListener( 'click', this.outsideClickHandler )
		this.setState( { openPopup: false } )
	}

	onKeyPressHandler( event ) {
		if ( event.target.closest( '.bwgb-url-input-popover' ) ||
			event.target.closest( '.components-popover' ) ) {
			return
		}
		this.hideUrlPopup()
	}

	render() {
		const {
			url = '',
			newTab = '',
			noFollow = '',
			onChangeUrl = null,
			onChangeNewTab = null,
			onChangeNoFollow = null,

			icon = null,

			isSelected = null,

			className = '',
			design = 'default',
			text = '',
			isBtn = true,
			background = 'primary',
			size = 'default',
			isIconButton = false,
			isWide = false,
			isWideSM = false,
			isBlock = false,
			isIconAfterText = true,
			isDisable = false,
			isTransition = true,
			borderRadius = 'default',
			onChange = () => {},
			dataAtts = {},
		} = this.props

		let color = ''

		for (let i = 0; i < paletteColor[0].length; i++) {
			if ( background === ( paletteColor[0][i].color || paletteColor[0][i].slug ) ) {
				color = paletteColor[0][i].slug
			}
		}
				
		const mainClasses = classnames( [
			className,
			'bwgb-button',
		], {
			'btn': isBtn,
			[`btn-${ color }`]: design === 'default' && color,
			[`btn-${ background }`]: design === 'default' && color === '',
			[`btn-${ design }-${ color }`]: ( design !== 'default' && design !== 'link' ) && color,
			[`btn-${ design }-${ background }`]: ( design !== 'default' && design !== 'link' ) && color === '',
			[`text-${ color }`]: design === 'link' && color,
			[`text-${ background }`]: design === 'link' && color === '',
			[`${ size }`]: size !== 'default',
			'btn-icon': isIconButton && design !== 'link',
			'btn-wide': isWide,
			'btn-sm-wide': isWideSM,
			'disabled btn-white': isDisable,
			'transition-3d-hover': isTransition,
			[`${ borderRadius }`]: borderRadius !== 'default',
			'btn-block' : isBlock
		} )

		const openUrlPopover = ( isSelected !== null ? isSelected : true ) && this.state.openPopup

		return (
			<div
				className={ mainClasses }
				onClick={ this.onButtonClickHandler }
				onKeyPress={ this.onKeyPressHandler }
				role="button"
				tabIndex="0"
				{ ...dataAtts }
			>
				{ ( icon && design !== 'link' && ! isIconAfterText ) &&
					<i className={ classnames( [ getIconClassName( icon ) ], { 'mr-1' : ! isIconButton } ) }></i>
				}
				{ ( ! isIconButton || design === 'link' ) &&
					<RichText
						tagName="span"
						placeholder={ __( 'Button text', i18n ) }
						value={ text }
						onChange={ onChange }
						withoutInteractiveFormatting={ true }
						keepPlaceholderOnFocus
					/>
				}
				{ ( icon && design !== 'link' && isIconAfterText ) &&
					<i className={ classnames( [ getIconClassName( icon ) ], { 'ml-1' : ! isIconButton } ) }></i>
				}
				{ openUrlPopover &&
					<UrlInputPopover
						value={ url }
						onChange={ onChangeUrl }
						newTab={ newTab }
						noFollow={ noFollow }
						onChangeNewTab={ onChangeNewTab }
						onChangeNoFollow={ onChangeNoFollow }
						disableSuggestions={ this.props.disableSuggestions }
					/>
				}
			</div>
		)
	}
}

ButtonEdit.defaultProps = {
	url: '',
	newTab: '',
	noFollow: '',
	onChangeUrl: null,
	onChangeNewTab: null,
	onChangeNoFollow: null,

	icon: null,

	isSelected: null,

	className: '',
	design: 'default',
	text: '',
	isBtn: true,
	background: 'primary',
	size: 'default',
	isIconButton: false,
	isWide: false,
	isWideSM: false,
	isBlock: false,
	isIconAfterText: true,
	isDisable: false,
	isTransition: true,
	borderRadius: 'default',
	onChange: () => {},
	dataAtts: {}
}

ButtonEdit.Content = props => {
	const {
		url = '',
		newTab = '',
		noFollow = '',
	
		icon = null,

		className = '',
		design = 'default',
		text = '',
		isBtn = true,
		background = 'primary',
		size = 'default',
		isIconButton = false,
		isWide = false,
		isWideSM = false,
		isDisable = false,
		isTransition = true,
		borderRadius = 'default',
		isBlock = false,
		isIconAfterText = true,
		target = '',
		dataAtts,
	} = props

	let color = ''

	for (let i = 0; i < paletteColor[0].length; i++) {
		if ( background === ( paletteColor[0][i].color || paletteColor[0][i].slug ) ) {
			color = paletteColor[0][i].slug
		}
	}

	const mainClasses = classnames( [
		className,
		'bwgb-button',
	], {
		'btn': isBtn,
		[`btn-${ color }`]: design === 'default' && color,
		[`btn-${ background }`]: design === 'default' && color === '',
		[`btn-${ design }-${ color }`]: ( design !== 'default' && design !== 'link' ) && color,
		[`btn-${ design }-${ background }`]: ( design !== 'default' && design !== 'link' ) && color === '',
		[`text-${ color }`]: design === 'link' && color,
		[`text-${ background }`]: design === 'link' && color === '',
		[`${ size }`]: size !== 'default',
		'btn-icon': isIconButton && design !== 'link',
		'btn-wide': isWide,
		'btn-sm-wide': isWideSM,
		'disabled btn-white': isDisable,
		'transition-3d-hover': isTransition,
		[`${ borderRadius }`]: borderRadius !== 'default',
		'btn-block' : isBlock
	} )

	const rel = []
	if ( newTab ) {
		rel.push( 'noopener' )
		rel.push( 'noreferrer' )
	}
	if ( noFollow ) {
		rel.push( 'nofollow' )
	}

	return (
		<Fragment>
			{ ( text || isIconButton ) &&
				<a
					className={ mainClasses }
					href={ url }
					target={ ( target || newTab ) ? ( target || '_blank' ) : undefined }
					rel={ props.rel || rel.join( ' ' ) }
					{ ...dataAtts }
				>
					{ ( icon && design !== 'link' && ! isIconAfterText ) &&
						<i className={ classnames( [ getIconClassName( icon ) ], { 'mr-1' : ! isIconButton } ) }></i>
					}
					{ ( ! isIconButton || design === 'link' ) &&
						<RichText.Content
							tagName="span"
							value={ text }
						/>
					}
					{ ( icon && design !== 'link' && isIconAfterText ) &&
						<i className={ classnames( [ getIconClassName( icon ) ], { 'ml-1' : ! isIconButton } ) }></i>
					}
				</a>
			}
		</Fragment>
	)
}

export default ButtonEdit
