/**
 * External dependencies
 */
import { Component } from '@wordpress/element'
import { createHigherOrderComponent } from '@wordpress/compose'
import PropTypes from 'prop-types'
import { formatError } from '../../util/errors.js'
import { 
	flatten, uniqBy, debounce 
} from 'lodash'

/**
 * Wordpress dependencies
 */
import { addQueryArgs } from '@wordpress/url'
import apiFetch from '@wordpress/api-fetch'

const getPostsRequests = ( {
	search = '',
	queryArgs = [],
} ) => {
	const defaultArgs = {
		per_page: -1,
		status: 'publish',
		search,
		orderby: 'title',
		order: 'asc',
	}
	const requests = [
		addQueryArgs( `wp/v2/posts`, { ...defaultArgs, ...queryArgs } ),
	]

	return requests
}

/**
 * Get a promise that resolves to a list of posts from the API.
 *
 * @param {Object} - A query object with the list of selected posts and search term.
 */
const getPosts = ( {
	selected = [],
	search = '',
	queryArgs = [],
} ) => {
	const requests = getPostsRequests( {
		selected,
		search,
		queryArgs
	} )

	return Promise.all( requests.map( path => apiFetch( { path } ) ) )
		.then( data => {
			const posts = uniqBy( flatten( data ), 'id' )
			const list = posts.map( post => ( {
				...post,
				parent: 0,
			} ) )
			return list
		} )
		.catch( e => {
			throw e
		} )
}

/**
 * A higher order component that enhances the provided component with posts
 * from a search query.
 */
const withSearchedPosts = createHigherOrderComponent(
	OriginalComponent => {

		/**
		 * A Component wrapping the passed in component.
		 *
		 * @class WrappedComponent
		 * @extends {Component}
		 */
		class WrappedComponent extends Component {
			constructor() {
				super( ...arguments )
				this.state = {
					list: [],
					loading: true,
				}
				this.setError = this.setError.bind( this )
				this.debouncedOnSearch = debounce(
					this.onSearch.bind( this ),
					400
				)
			}

			componentDidMount() {
				const { selected } = this.props
				getPosts( { selected } )
					.then( list => {
						this.setState( { list, loading: false } )
					} )
					.catch( this.setError )
			}

			componentWillUnmount() {
				this.debouncedOnSearch.cancel()
			}

			onSearch( search ) {
				const { selected } = this.props

				getPosts( { selected, search } )
					.then( list => {
						this.setState( { list, loading: false } )
					} )
					.catch( this.setError )
			}

			async setError( e ) {
				const error = await formatError( e )

				this.setState( {
					list: [],
					loading: false,
					error
				} )
			}

			render() {
				const {
					error,
					list,
					loading
				} = this.state

				return (
					<OriginalComponent
						{ ...this.props }
						error={ error }
						posts={ list }
						isLoading={ loading }
						onSearch={
							search => {
								this.setState( { loading: true } )
								this.debouncedOnSearch( search )
							}
						}
					/>
				)
			}
		}
		WrappedComponent.propTypes = {
			selected: PropTypes.array,
		}
		WrappedComponent.defaultProps = {
			selected: [],
		}
		return WrappedComponent
	},
	'withSearchedPosts'
)

export default withSearchedPosts
