import { __ } from '@wordpress/i18n'
import { compose } from '@wordpress/compose'
import { 
	withSelect, 
	withDispatch 
} from '@wordpress/data'
import { 
	PanelBody, 
	ToggleControl, 
	SelectControl, 
	TextControl 
} from '@wordpress/components'
import { PostTypeCheck, PostIDSelector } from '~bookwormgb/components'
import { Fragment } from '@wordpress/element'
import { i18n } from 'bookwormgb'

const PagePluginSidebar = props => {
	const { 
		body_classes, 
		setBodyClasses, 
		disable_container, 
		setContainer, 
		hide_page_header, 
		setHidePageHeader, 
		header_style, 
		setHeaderStyle,
		is_custom_header = false,
		header_static_content_id,
		setHeaderStaticContentId,
		setIsCustomHeader,
		footer_static_content_id,
		setFooterStaticContentId,
		is_custom_footer = false,
		setIsCustomFooter,
		footer_style, 
		setFooterStyle,
		hide_page_breadcrumb,
		setHidePageBreadcrumb
	} = props

	return(
		<Fragment>
			<PostTypeCheck name="page">
				<PanelBody title={ __( 'General', i18n ) } initialOpen={ false }>
					<TextControl
						label={ __('Body Classes', i18n) }
						value={ body_classes }
						onChange={ body_classes => { setBodyClasses( body_classes )} }
					/>
					<ToggleControl
						label={ __( 'Disable Container ?', i18n ) }
						checked={ disable_container }
						onChange={ disable_container => { setContainer( disable_container )} }
					/>
					<ToggleControl
						label={ __( 'Hide Page Header', i18n ) }
						checked={ hide_page_header }
						onChange={ hide_page_header => { setHidePageHeader( hide_page_header )} }
					/>
					<ToggleControl
						label={ __( 'Hide Page Breadcrumb', i18n ) }
						checked={ hide_page_breadcrumb }
						onChange={ hide_page_breadcrumb => { setHidePageBreadcrumb( hide_page_breadcrumb )} }
					/>
				</PanelBody>
				<PanelBody title={ __( 'Header', i18n ) } initialOpen={ false }>
					<ToggleControl
						label={ __( 'Enable Custom Header', i18n ) }
						checked={ is_custom_header }
						onChange={ is_custom_header => { setIsCustomHeader( is_custom_header ) } }
					/>
					{ is_custom_header && (
						<PostIDSelector
							onPostSelect={ header_static_content_id => {
								setHeaderStaticContentId( header_static_content_id ) 
							} }
							posts={ header_static_content_id ? [ header_static_content_id ] : [] }
							onChange={ headerStaticContentIDs => {
								if( headerStaticContentIDs && headerStaticContentIDs.length ) {
									setHeaderStaticContentId( { header_static_content_id : headerStaticContentIDs[0] } )
								} else {

									setHeaderStaticContentId( 0 )
								}
							} }
							postType="mas_static_content"
							limit="1"
						/>
					) }
					{ ! is_custom_header && (
						<SelectControl
							label={ __( 'Header Style', i18n ) }
							value={ header_style }
							options={ [
								{ label: __( 'Default', i18n ), value: '' },
								{ label: __( 'Header v1', i18n ), value: 'v1' },
								{ label: __( 'Header v2', i18n ), value: 'v2' },
								{ label: __( 'Header v3', i18n ), value: 'v3' },
								{ label: __( 'Header v4', i18n ), value: 'v4' },
								{ label: __( 'Header v5', i18n ), value: 'v5' },
								{ label: __( 'Header v6', i18n ), value: 'v6' },
								{ label: __( 'Header v7', i18n ), value: 'v7' },
								{ label: __( 'Header v8', i18n ), value: 'v8' },
								{ label: __( 'Header v9', i18n ), value: 'v9' },
								{ label: __( 'Header v10', i18n ), value: 'v10' },
								{ label: __( 'Header v11', i18n ), value: 'v11' },
								{ label: __( 'Header v12', i18n ), value: 'v12' },
								{ label: __( 'Header v13', i18n ), value: 'v13' },
							] }
							onChange={ setHeaderStyle }
						/>
					) }
				</PanelBody>
				<PanelBody title={ __( 'Footer', i18n ) } initialOpen={ false }>
					<ToggleControl
						label={ __( 'Enable Custom Footer', i18n ) }
						checked={ is_custom_footer }
						onChange={ is_custom_footer => { setIsCustomFooter( is_custom_footer ) } }
					/>
					{ is_custom_footer && (
						<PostIDSelector
							onPostSelect={ footer_static_content_id => {
								setFooterStaticContentId( footer_static_content_id ) 
							} }
							posts={ footer_static_content_id ? [ footer_static_content_id ] : [] }
							onChange={ footerStaticContentIDs => {
								if( footerStaticContentIDs && footerStaticContentIDs.length ) {
									setFooterStaticContentId( { footer_static_content_id : footerStaticContentIDs[0] } )
								} else {
									setFooterStaticContentId( 0 )
								}
							} }
							postType="mas_static_content"
							limit="1"
						/>

					) }
					{ ! is_custom_footer && (

						<SelectControl
							label={ __( 'Footer Style', i18n ) }
							value={ footer_style }
							options={ [
								{ label: __( 'Default', i18n ), value: '' },
								{ label: __( 'Footer v1', i18n ), value: 'v1' },
								{ label: __( 'Footer v2', i18n ), value: 'v2' },
								{ label: __( 'Footer v3', i18n ), value: 'v3' },
								{ label: __( 'Footer v4', i18n ), value: 'v4' },
								{ label: __( 'Footer v5', i18n ), value: 'v5' },
								{ label: __( 'Footer v6', i18n ), value: 'v6' },
								{ label: __( 'Footer v7', i18n ), value: 'v7' },
								{ label: __( 'Footer v8', i18n ), value: 'v8' },
								{ label: __( 'Footer v9', i18n ), value: 'v9' },
								{ label: __( 'Footer v10', i18n ), value: 'v10' },
								{ label: __( 'Footer v11', i18n ), value: 'v11' },
								{ label: __( 'Footer v12', i18n ), value: 'v12' },
								{ label: __( 'Footer v13', i18n ), value: 'v13' },
							] }
							onChange={ setFooterStyle }
						/>
					) }
				</PanelBody>
			</PostTypeCheck>
		</Fragment>
	)
}

export default compose(
	withSelect( select => {
		const { getEditedPostAttribute } = select( 'core/editor' )
		const meta = getEditedPostAttribute( 'meta' ) || {}
		const { 
			_body_classes, 
			_disable_container, 
			_hide_page_header, 
			_header_style ,
			_is_custom_header,
			_header_static_content_id,
			_footer_static_content_id,
			_is_custom_footer,
			_footer_style,
			_hide_page_breadcrumb
		} = meta
		return {
			body_classes: _body_classes,
			disable_container: _disable_container,
			hide_page_header: _hide_page_header,
			header_style: _header_style,
			footer_style: _footer_style,
			is_custom_header: _is_custom_header,
			header_static_content_id: _header_static_content_id,
			footer_static_content_id: _footer_static_content_id,
			is_custom_footer: _is_custom_footer,
			hide_page_breadcrumb: _hide_page_breadcrumb
		}
	} ),
	withDispatch( dispatch => ( {
		setBodyClasses( value ) {
			dispatch( 'core/editor' ).editPost( { meta: { _body_classes: value } } )
		},

		setContainer( value ) {
			dispatch( 'core/editor' ).editPost( { meta: { _disable_container: value } } )
		},

		setHidePageHeader( value ) {
			dispatch( 'core/editor' ).editPost( { meta: { _hide_page_header: value } } )
		},

		setHidePageBreadcrumb( value ) {
			dispatch( 'core/editor' ).editPost( { meta: { _hide_page_breadcrumb: value } } )
		},

		setHeaderStyle( value ) {
			dispatch( 'core/editor' ).editPost( { meta: { _header_style: value } } )
		},

		setFooterStyle( value ) {
			dispatch( 'core/editor' ).editPost( { meta: { _footer_style: value } } )
		},

		setIsCustomHeader( value ) {
			dispatch( 'core/editor' ).editPost( { meta: { _is_custom_header: value } } )
		},

		setIsCustomFooter( value ) {
			dispatch( 'core/editor' ).editPost( { meta: { _is_custom_footer: value } } )
		},

		setHeaderStaticContentId( value ) {
			dispatch( 'core/editor' ).editPost( { meta: { _header_static_content_id: value } } )
		},

		setFooterStaticContentId( value ) {
			dispatch( 'core/editor' ).editPost( { meta: { _footer_static_content_id: value } } )
		},
	} ) ),
)( PagePluginSidebar )