/**
 * External dependencies
 */
import { ButtonControls } from '~bookwormgb/components'
import { camelCase } from 'lodash'

/**
 * WordPress dependencies
 */
import { sprintf } from '@wordpress/i18n'

const ButtonControlsHelper = props => {
	const { setAttributes } = props
	const getAttrName = attrName => camelCase( sprintf( props.attrNameTemplate, attrName ) )

	return (
		<ButtonControls
			design={ props.blockAttributes[ getAttrName( 'Design', 'default' ) ] }
			onChangeDesign={ value => setAttributes( { [ getAttrName( 'Design' ) ]: value } ) }

			showUrl={ props.blockAttributes[ getAttrName( 'showUrl' ) ] }
			url={ props.blockAttributes[ getAttrName( 'Url' ) ] || '' }
			newTab={ props.blockAttributes[ getAttrName( 'NewTab' ) ] || '' }
			noFollow={ props.blockAttributes[ getAttrName( 'NoFollow' ) ] || '' }
			onChangeUrl={ value => setAttributes( { [ getAttrName( 'Url' ) ]: value } ) }
			onChangeNewTab={ value => setAttributes( { [ getAttrName( 'NewTab' ) ]: value } ) }
			onChangeNoFollow={ value => setAttributes( { [ getAttrName( 'NoFollow' ) ]: value } ) }

			background={ props.blockAttributes[ getAttrName( 'Background' ) ] || '' }
			onChangeBackground={ value => setAttributes( { [ getAttrName( 'Background' ) ]: value } ) }

			size={ props.blockAttributes[ getAttrName( 'Size', 'default' ) ] }
			onChangeSize={ value => setAttributes( { [ getAttrName( 'Size' ) ]: value } ) }

			isWide={ props.blockAttributes[ getAttrName( 'IsWide' ) ] || '' }
			onChangeWide={ value => setAttributes( { [ getAttrName( 'IsWide' ) ]: value } ) }

			isWideSM={ props.blockAttributes[ getAttrName( 'IsWideSM' ) ] || '' }
			onChangeWideSM={ value => setAttributes( { [ getAttrName( 'IsWideSM' ) ]: value } ) }

			isDisable={ props.blockAttributes[ getAttrName( 'IsDisable' ) ] || '' }
			onChangeDisable={ value => setAttributes( { [ getAttrName( 'IsDisable' ) ]: value } ) }

			isBlock={ props.blockAttributes[ getAttrName( 'IsBlock' ) ] || '' }
			onChangeBlock={ value => setAttributes( { [ getAttrName( 'IsBlock' ) ]: value } ) }

			borderRadius={ props.blockAttributes[ getAttrName( 'BorderRadius', 'default' ) ] }
			onChangeBorderRadius={ value => setAttributes( { [ getAttrName( 'BorderRadius' ) ]: value } ) }

			isIconAfterText={ props.blockAttributes[ getAttrName( 'IsIconAfterText' ) ] }
			onChangeIconAfterText={ value => setAttributes( { [ getAttrName( 'IsIconAfterText' ) ]: value } ) }

			isIconButton={ props.blockAttributes[ getAttrName( 'IsIconButton' ) ] || '' }
			onChangeIconButton={ value => setAttributes( { [ getAttrName( 'IsIconButton' ) ]: value } ) }

			isTransition={ props.blockAttributes[ getAttrName( 'IsTransition' ) ] || '' }
			onChangeTransition={ value => setAttributes( { [ getAttrName( 'IsTransition' ) ]: value } ) }

			icon={ props.blockAttributes[ getAttrName( 'Icon' ) ] || '' }
			onChangeIcon={ value => setAttributes( { [ getAttrName( 'Icon' ) ]: value } ) }
			{ ...props }
		/>
	)
}

ButtonControlsHelper.defaultProps = {
	attrNameTemplate: '%s',
	setAttributes: () => {},
	blockAttributes: {},
}

export default ButtonControlsHelper
